import TreeNode from './TreeNode.vue';

export default {
  name: 'tree',

  props: {
    objects: Array,
    level: {
      type: Number,
      required: false,
      default: 0
    },
    spanLevel: {
      type: Array,
      required: false,
      default: () => []
    },
    dragObject: {
      type: Object,
      required: true
    },
    dragOver: {
      type: Object,
      required: true
    },
    dragBefore: {
      type: Object,
      required: true
    },
    dragAfter: {
      type: Object,
      required: true
    },
    dragTarget: {
      type: Object,
      required: true
    },
    scrollbar: {
      required: true,
      validator: (scrollbar) => {
        return ['object', 'undefined'].indexOf(typeof scrollbar) !== -1;
      }
    }
  },

  components: {
    TreeNode
  },

  methods: {

    isLast(index) {
      return (index + 1) === this.objects.length;
    },

    spanLevels(level, isLast) {
      if (level === 0) {
        return [level];
      }

      if (isLast) {
        return this.spanLevel;
      }

      return this.spanLevel.concat([level]);
    }
  }
}



