import WasabiLogo from '../../../components/Logo.vue';

export default {
  name: 'tree-root-node',

  components: {
    WasabiLogo
  },

  props: {
    object: {
      type: Object,
      required: true
    }
  },

  computed: {
    node() {
      return this.object;
    },

    assetUrl() {
      return window.WS.getModule('Wasabi/Cms').options.assetUrl;
    },

    imageUrl() {
      return this.assetUrl + '/img/logo.svg'
    }
  }
}



